<template>
  <div class="commit">
    <button class="commit-btn" @click="Commit">{{ title }}</button>
  </div>
</template>
<script>
export default {
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000Z1'
    }
  },
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    commit: {
      type: Boolean
    },
    title: {
      type: String,
      default: '提交',
      require: false
    }
  },
  methods: {
    Commit () {
      if (this.commit) {
        this.$emit('commit', {
          schemaKey: this.schemaKey,
          uuid: this.uuid
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.commit
  text-align center
  margin-top 1.5rem
  .commit-btn
    height 50px
    width 205px
    font-size 15px
    color #fff
    background-color #f39938
    border-radius 50px
    outline none
    border none
    transition background-color .3s linear
    cursor pointer
    &:hover
      background-color #ffae57
</style>
